@import url(https://fonts.googleapis.com/css?family=Open+Sans|Shadows+Into+Light);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { box-sizing: border-box; }

.App {
  text-align: center;
  background-image: url(/gusRichardsonProject05/static/media/cloudyDay.4d7e3ee4.jpg);
  background-size: cover;
  background-position: middle;
  background-repeat: no-repeat;
  min-height: 100vh;
  font-size: 125%;
}

button {
  height: 30px;
  width: 160px;
  border-radius: 5%;
  border: 4px solid rgb(114, 154, 169);
  background: rgb(255, 183, 168);
  margin: 10px 10px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  text-transform: capitalize;
}

button:hover {
  border: 4px solid rgb(83, 130, 148);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

button:focus {
  background: rgb(236, 147, 128);
  border: 4px solid rgb(57, 110, 130);
}

label {
  text-transform: capitalize;
}

input {
  background: rgb(255, 227, 221);
  text-transform: capitalize;
}

input:focus {
  border: 4px solid rgb(114, 154, 169);
}

header {
  height: 20vh;
}

#low {
  display: block;
}

header h1 {
  margin: 0px;
  padding-top: 20px;
  font-size: 80px;
  font-family: 'Shadows Into Light', cursive;
}

li {
  list-style: none;
}

#latitude {
  margin-right: 20px;
}
#longLabel {
  margin-left: 20px;
}

.weatherInput {
  height: 5vh;
}

.weatherContainer {
  height: 35vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  text-align: left;
  padding: 0 40px 40px 40px;
}



.dayForecast {
  margin-top: 20px;
  width: 14%;
  height: 100%;
  font-size: 16px;
  display: -webkit-flex;
  display: flex;
  padding-bottom: 10px;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background: rgba(255, 255, 255, 0.4);
}

p {
  margin: 0 10px;
  font-family: 'Open Sans', sans-serif;

}

img {
  width: 100%;
  height: 100px;
  display: block;

}

.clothingItems {
  min-height: 40vh;
  display: -webkit-flex;
  display: flex;
  padding: 0 40px 20px 40px;
}

.clothingItems label {
  color: black;
  background: rgba(255,255,255,0.5);
  width: 160px;
}

.suitcaseList{
 padding-left: 20px;
  width: 50%;
  background-image: 
  -webkit-linear-gradient(bottom, rgba(255,255,255,0.7) 5em, rgba(255,0,0,.2) 0, rgba(255,255,255,0.7) 5.1em), 
  -webkit-linear-gradient(rgba(0,0,255,.8) 1px, rgba(255,255,255,0.7) 0);
  background-image: 
  linear-gradient(0deg, rgba(255,255,255,0.7) 5em, rgba(255,0,0,.2) 0, rgba(255,255,255,0.7) 5.1em), 
  linear-gradient(rgba(0,0,255,.8) 1px, rgba(255,255,255,0.7) 0);
  background-size: 60% 2em;  
  box-shadow: 10px 10px 10px rgba(16,16,16, 0.9);
  position: relative;
  
}

#listDelete {
  position: absolute;
  bottom: 10px;
  right: 0;
}

.suitcaseList h2 {
  margin-top: 0;
  margin-bottom: 0px;
  padding-bottom: 0;
  text-align: left;
  font-family: 'Shadows Into Light', cursive;
  text-decoration: underline;
}

.suitcaseList ul {
  font-family: 'Shadows Into Light', cursive;
  font-size: 24px;
  text-align: left;
  line-height: 1.6em;
  margin-top: 0;
  padding-top: 0;
  padding-left: 0;

}

ul li {
  margin-top: 0;
  padding-top: 0;
}

.clothingForm {
  padding-top: 110px;
  width: 50%;
  background: url(/gusRichardsonProject05/static/media/brownSuitCase.9c36b7b6.png);
  background-size: 70% 100%;
  background-repeat: no-repeat;
  background-position: center center;
}

.clothingForm input, .clothingForm button {
  display: block;
  margin: 0 auto;
  width: 200px;
}

