@media screen and (max-width: 1000px) {
    .App {
        height: auto;
    }

    .weatherInput {
        height: auto;
    }

    .weatherContainer {
        height: auto;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        -webkit-align-items: center;
                align-items: center;
    }

    .dayForecast {
        width: 22%;
        height: 240px;
        margin: 10px;
    }

}

@media screen and (max-width: 900px) {
    .inputMobile {
        display: block;
        margin: 8px auto;
    }
    .dayForecast {
        width: 30%;
        height: 240px;
        margin: 5px;
    }
    #latitude, #longitude {
        margin-right: 0px;
        text-align: center;
      }
    #longLabel {
        margin-left: 0px;
    }
    .weatherInput {
        height: auto;
    }
    #forecastButton {
        width: 220px;
    }
    .clothingItems {
        height: auto;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
    }

    .clothingForm {
        width: 80%;
        height: 280px;
    }
    .suitcaseList {
        margin-top: 40px;
        width: 80%;
        min-height: 340px;
    }

}

@media screen and (max-width: 700px) {
    .dayForecast {
        width: 45%;
    }
    .clothingForm {
        background-size: 90% 100%;
      }
}

@media screen and (max-width: 500px){
    header {
        height: auto;
        line-height: 100px;
    }
}

@media screen and (max-width: 450px){
    .weatherContainer {
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
    }
    .dayForecast {
        width: 90%;
    }

    .clothingForm {
        width: 100%;
    }

    .suitcaseList {
        margin-top: 40px;
        width: 100%;
        min-height: 340px;
    }
}
